<template>
  <div>
    <LayOut>
      <template #content>
        <p v-if="work_total_time!=0">> 累计工作时间:{{work_total_time}}小时</p>
        <a-form-model layout="inline" :model="formInline">
          <a-form-model-item>
            <a-input v-model="formInline.real_name" placeholder="服务人员姓名"></a-input>
          </a-form-model-item>
          <a-form-model-item>
            <a-input v-model="formInline.cus_name" placeholder="服务对象姓名"></a-input>
          </a-form-model-item>
          <a-form-model-item label="开始日期" required prop="startTime">
            <a-date-picker
              v-model="formInline.startTime"
              type="date"
              placeholder="开始日期"
              style="width: 100%;"
            />
          </a-form-model-item>
          <a-form-model-item label="结束日期" required prop="endTime">
            <a-date-picker
              v-model="formInline.endTime"
              type="date"
              placeholder="结束日期"
              style="width: 100%;"
            />
          </a-form-model-item>
          <a-form-model-item>
            <a-button @click="onSubmit" type="primary" html-type="submit">查询</a-button>
          </a-form-model-item>
        </a-form-model>
        <a-divider></a-divider>
        <a-table :columns="columns" :pagination="pagination" :data-source="data"></a-table>
      </template>
    </LayOut>
  </div>
</template>
<script>
import { queryClock } from "@/api/api";
import LayOut from "@/views/LayOut.vue";
const columns = [
  {
    dataIndex: "real_name",
    key: "real_name",
    title: "服务人员姓名"
  },
  {
    title: "服务对象姓名",
    dataIndex: "cus_name",
    key: "cus_name"
  },
  {
    title: "上班时间",
    dataIndex: "on_duty_time",
    key: "on_duty_time"
  },
  {
    title: "下班时间",
    key: "off_duty_time",
    dataIndex: "off_duty_time"
  },
  {
    title: "工作时长",
    key: "timediff",
    dataIndex: "timediff"
  }
];

const data = [];
export default {
  data() {
    return {
      work_total_time:0,
      pagination: {
        total: 0,
        pageSize: 10, //每页中显示10条数据
        showSizeChanger: true,
        pageSizeOptions: ["10", "20", "50", "100"], //每页中显示的数据
        showTotal: total => `共有 ${total} 条数据` //分页中显示总的数据
      },
      data,
      columns,
      formInline: {}
    };
  },
  methods: {
    async onSubmit(e) {
      let res = await queryClock(this.formInline);
      let totaltime = 0;
      for (let i = 0; i < res.length; i++) {
        
        if (res[i].off_duty_time) {
          
          res[i].timediff = (
            (new Date(res[i].off_duty_time).getTime() -
              new Date(res[i].on_duty_time).getTime()) /
            3600 /
            1000
          ).toFixed(3);
          res[i].off_duty_time = this.$moment(res[i].off_duty_time).format("YYYY-MM-DD HH:MM")
          res[i].on_duty_time = this.$moment(res[i].on_duty_time).format("YYYY-MM-DD HH:MM")
          totaltime += parseFloat(res[i].timediff) ;
        } else {
          res[i].on_duty_time = this.$moment(res[i].on_duty_time).format("YYYY-MM-DD HH:MM")
          res[i].timediff = "下班未打卡";
        } 
      }
      this.work_total_time = totaltime.toFixed(3)
      this.data = res;
    }
  },
  components: {
    LayOut
  }
};
</script>
