<template>
  <div>
    <LayOut>
      <template #content>
        <a-form-model layout="inline" :model="formInline">
          <a-form-model-item>
            <a-input v-model="formInline.real_name" placeholder="姓名"></a-input>
          </a-form-model-item>
          <a-form-model-item>
            <a-input v-model="formInline.idCard" placeholder="身份证"></a-input>
          </a-form-model-item>
          <a-form-model-item>
            <a-input v-model="formInline.contact" placeholder="联系方式"></a-input>
          </a-form-model-item>
          <a-form-model-item>
            <a-button @click="onSubmit" type="primary" html-type="submit">查询</a-button>
          </a-form-model-item>
        </a-form-model>
        <a-divider></a-divider>
        <a-table :columns="columns" :pagination="pagination" :data-source="data">
          <span slot="action" slot-scope="record">
            <router-link :to="{path:'/assistantdetail',query:{real_name:record.real_name}}">详情</router-link>
            <a-divider type="vertical" />

            <a-popconfirm
              title="您确定要删除吗?"
              ok-text="Yes"
              cancel-text="No"
              @confirm="confirm(record.real_name)"
              @cancel="cancel"
            >
              <a>删除</a>
            </a-popconfirm>
            <a-divider type="vertical" />
            <router-link :to="{path:'/servicepersons',query:{real_name:record.real_name}}">服务对象</router-link>
          </span>
        </a-table>
      </template>
    </LayOut>
  </div>
</template>
<script>
import { initAssistants } from "@/api/api";
import { queryAssistants } from "@/api/api";
import { delAssistant } from "@/api/api";
import LayOut from "@/views/LayOut.vue";
const columns = [
  {
    dataIndex: "real_name",
    key: "real_name",
    title: "姓名"
  },
  {
    title: "联系方式",
    dataIndex: "contact",
    key: "contact"
  },
  {
    title: "居住地址",
    dataIndex: "address",
    key: "address"
  },
  {
    title: "证书",
    key: "certificate",
    dataIndex: "certificate"
  },
  {
    title: "服务户数",
    key: "serverNum",
    dataIndex: "serverNum"
  },
  {
    title: "工作年限",
    key: "workSeniority",
    dataIndex: "workSeniority"
  },
  {
    title: "保险",
    key: "insurance",
    dataIndex: "insurance"
  },
  {
    title: "银行卡号",
    key: "bankCard",
    dataIndex: "bankCard"
  },
  {
    title: "操作",
    key: "action",
    scopedSlots: { customRender: "action" }
  }
];

const data = [];
export default {
  data() {
    return {
      pagination: {
        total: 0,
        pageSize: 10, //每页中显示10条数据
        showSizeChanger: true,
        pageSizeOptions: ["10", "20", "50", "100"], //每页中显示的数据
        showTotal: total => `共有 ${total} 条数据` //分页中显示总的数据
      },
      data,
      columns,
      formInline: {
        real_name: "",
        idCard: "",
        assistantName: "",
        disabledContact: "",
        userType: "",
        district: ""
      }
    };
  },
  async mounted() {
    let res = await initAssistants();
    this.data = res;
  },
  methods: {
    async onSubmit(e) {
      console.log(this.formInline);
      let res = await queryAssistants(this.formInline);
      this.data = res;
    },
    async confirm(e) {
      let res = await delAssistant(e);
      if (res.data.errmsg == "ok") {
        this.$message.success("删除成功");
        let res = await initAssistants();
        this.data = res;
      }
    },
    cancel(e) {
      console.log(e);
      // this.$message.error("点击 No");
    }
  },
  components: {
    LayOut
  }
};
</script>
